body {
  width: 100%;
  font-family: Calibri;
  position: relative;
}

nav {
  width: max-content;
  margin-top: 40px;
  position: relative;
  left: 50%;
  transform: translate(-50%);
}

.nav-items {
  grid-gap: 20px;
  grid-auto-columns: max-content;
  grid-auto-flow: column;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
  display: grid;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

li > a {
  color: #666;
  border-radius: 10px;
  padding: 15px 20px;
  text-decoration: none;
}

li > a:hover {
  color: #000;
  background-color: #f2f2f2;
}

a {
  color: #333;
  font-size: 18px;
  text-decoration: underline;
}

.header {
  width: 100%;
  margin-top: 80px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.header > h1, h2 {
  text-align: center;
  margin: 10px;
  font-size: 48px;
}

.header > p {
  text-align: center;
  color: #666;
  margin: 0;
  padding: 0;
  font-size: 18px;
}

.code-block {
  background-color: #f7f7f7;
  border: 1px solid #f2f2f2;
  border-radius: 10px;
  padding: 20px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.code-block > h2 {
  text-align: left;
  margin: 0;
  padding: 0;
  font-size: 24px;
}

.code-block > p {
  color: #333;
  height: 50px;
  font-size: 20px;
}

.code-block > pre {
  margin: 0;
  font-size: 16px;
}

code {
  border-radius: 10px;
  background-color: #fff !important;
}

.example {
  width: max-content;
  grid-gap: 10px;
  grid-template-rows: max-content max-content max-content max-content;
  grid-template-columns: 1fr 1fr;
  margin-top: 100px;
  display: grid;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.component-block {
  grid-area: 1 / 1 / 2 / 2;
}

.processor-block {
  grid-area: 1 / 2 / 2 / 3;
}

.entity-block {
  grid-area: 2 / 1 / 3 / 3;
}

.update-block {
  grid-area: 3 / 1 / 4 / 3;
}

.example-header {
  margin: 50px 0;
}

button {
  cursor: pointer;
  background-color: #f2f2f2;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
}

#reset-button {
  color: #023;
  background-color: #6cf;
}

.basic-game {
  grid-gap: 20px;
  width: max-content;
  grid-template-columns: auto 1fr;
  display: grid;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.game-source {
  text-align: center;
  margin-top: 30px;
  display: block;
}

.controls {
  grid-column: 2 / 3;
}

.buttons {
  width: max-content;
  grid-gap: 5px;
  display: grid;
}

.checkboxes {
  grid-gap: 20px;
  width: max-content;
  margin-bottom: 50px;
  display: grid;
}

canvas {
  max-width: 500px;
  grid-column: 1 / 2;
}

.information-container {
  width: max-content;
  grid-gap: 20px;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 50px 0;
  display: grid;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.information-card {
  background-color: #f7f7f7;
  border: 1px solid #f2f2f2;
  border-radius: 10px;
  padding: 30px 20px;
}

.information-card > h3 {
  color: #666;
  margin: 0;
  font-size: 16px;
  font-weight: 500;
}

.information-card > p {
  margin-top: 5px;
  margin-bottom: 0;
  font-size: 24px;
  font-weight: 600;
}

.copyright {
  color: #333;
  width: max-content;
  margin: 40px 0;
  font-size: 20px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.copyright > a {
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
}

